@charset "UTF-8";
html {
  height: 100%;
  scroll-behavior: smooth;
}

html, body {
  min-height: 100%;
  margin: 0;
}

body {
  background-attachment: fixed;
  background: no-repeat center center black;
  background-size: cover;
  font-family: Poppins;
}

h1, h2, h3, h4 {
  text-transform: lowercase;
}

header {
  z-index: 10;
  position: fixed;
  top: 20px;
  left: 20px;
  width: 100%;
  max-width: 200px;
}

@media screen and (min-width: 767px) {
  header {
    top: 40px;
    left: 40px;
  }
}

.dummy {
  width: 1px;
  height: 1px;
  opacity: 0;
}

.wrapper {
  min-width: 100%;
  min-height: 100%;
  position: relative;
}

.wrapper .contenu {
  position: relative;
  z-index: 1;
}

.wrapper .illustration {
  opacity: 0;
  transition: opacity .3s ease;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 0;
  background-attachment: fixed;
  background: no-repeat center center;
  background-size: cover;
}

.wrapper .illustration.visible {
  opacity: 1;
}

.wrapper .illustration video {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

a {
  color: white;
  text-decoration: none;
  transition: color .3s ease;
}

a:hover {
  color: black;
}

.albums li a {
  font-family: Poppins;
  font-size: 40px;
  text-transform: lowercase;
}

.albums li a:after {
  opacity: 0;
  display: inline-block;
  content: 'ðŸ¡°';
  margin-left: .5em;
}

.albums li a:hover {
  background: white;
}

.albums li a:hover:after {
  opacity: 1;
}

@media screen and (min-width: 767px) {
  .albums {
    margin-top: 300px;
    margin-left: 150px;
  }
}

.album .hero {
  position: relative;
}

.album .document {
  background: no-repeat center center black;
  background-size: cover;
}

.album .lien-slide {
  z-index: 1000;
  text-shadow: 1px 1px 7px rgba(0, 0, 0, 0.5);
  user-select: none;
  width: 100px;
  text-align: center;
  font-size: 40px;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}

.album .lien-slide:before {
  display: block;
}

.album .lien-slide.next-slide {
  bottom: 15px;
}

.album .lien-slide.next-slide:before {
  content: 'ðŸ¢ƒ';
}

.album .lien-slide.prev-slide {
  top: 15px;
}

.album .lien-slide.prev-slide:before {
  content: 'ðŸ¡¹';
}

.page .container {
  padding-top: 80px;
}

@media screen and (min-width: 767px) {
  .page .container {
    padding-top: 0px;
  }
}
